.container{
  /* width: 1200px;
  max-width: 1920px; */
  margin: 0 auto;
}
.ml-auto{
  margin-left: auto;
}

.float-right{
  float: right;
}

.float-left{
  float: left;
}

@media screen and (min-width:768px){
  .float-md-right{
    float: right;
  }
  
  .float-md-left{
    float: left;
  }
}

@media screen and (max-width:768px){
  .container{
    margin: 0;
  }
}